import uniPopup from '@/components/uni-popup/uni-popup'
export default {
	props: {
		onChange: {
			type: Function,
			default: () => {}
		}
	},
	components: {
		uniPopup
	},
	data() {
		return {
			data: [],
			value: 0,
			type: ''
		};
	},
	mounted() {
		this.getData()
	},
	methods: {
		handleChange(e) {
			this.value = e.detail.value
			this.$emit("onChange", this.data[e.detail.value]);
			// this.onChange(this.data[e.detail.value])
		},
		add() {
			this.$refs.specPop.open()
		},
		getData() {
			this.$getJson('/api/v3/store/goods/category/list.jsp', {
				'storeId': 203
			}, 'GET', res => {
				this.data = res.data
			})
		},
		saveType() {
			this.$getJson(
				'/api/v3/store/goods/category/addOrUpdate.jsp', {
					storeId: 203,
					name: this.type
				},
				'POST',
				res => {
					this.$refs.specPop.close()
					this.getData()
				}
			);


		}
	}
}
